@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
#app{
    font-family: 'Nunito', sans-serif;
    min-width: 100vw;
    min-height: 100vh;
    width: 100%;
    height: 100%;
    background-color: #cfd9d0;
}

#app-container{
    max-width: 1200px;
    margin: 0 auto;
}

.logo{
    width: 3rem;
}

@media(max-width: 1200px){
    #app-container{
        padding: 1rem 2rem;
    }
}

nav, #nav-logo, #nav-links{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#nav-logo strong{
    color: #847F80;
    padding: 0 .3rem ;
}

#nav-links a{
    color: #847F80;
    padding: 0 1rem ;
}

#nav-links a:hover{
    color: #ff3535;
}

#nav-button button{
    cursor: pointer;
    padding: .2rem .5rem;
    background-color: #fff;
    color: #ff3535;
    border-radius: 16px;
    box-shadow: rgb(226, 214, 207) 4px 4px 8px;
}

#nav-button button:hover{
    transform: scale(1.1);
}

@media(max-width: 750px){
    #nav-links a{
        padding: 0 .7rem ;
    }    
}

@media(max-width: 450px){
    #nav-links a{
        padding: 0 .5rem ;
    }    

    #nav-logo p{
        display: none;
    }
}


@media(max-width: 400px){
    #nav-links a{
        padding: 0 .3rem ;
    }    

    nav{
        font-size: .8rem;
    }
}


#logo{
    margin-bottom: 1.5rem;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#logo img{
    width: 12rem;
}

#logo h1{
    position: absolute;
    bottom: -20px;
    color: #5A5557;
}

@media(max-width: 750px){
    #logo img{
        width: 10rem;
    }    
}

@media(max-width: 450px){
    #logo img{
        width: 8rem;
    }    
}

#home{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 800px;
    margin: 0 auto;
}

#home h3, 
#home button{
    margin-top: 1rem;
}

#home h3{
    color: #5A5557;
    text-align: center;
}

#home button{
    font-size: larger;
    padding: 1rem;
    width: 100%;
    background-color: rgb(240, 233, 231);
    border-radius: 20px;
    box-shadow: rgb(226, 214, 207) 6px 6px 12px;
}

#home button a{
    color: #dd6a0d;
}

#home button:hover{
    background-color: rgb(247, 244, 242);
}

#cards{
    width: 100%;
    margin-top: 2rem;
    color: #b7a393;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
    justify-content: space-between;
}

.card{
    display: grid;
    grid-template-rows: 1fr;
    background-color: #F0E9E7;
    border: 1px solid rgb(226, 214, 207);
    border-radius: 10px;
    box-shadow: rgb(247, 244, 242) 1px 1px 0px inset;
}

#card-1{
    padding: 2rem;
    display: flex;
}

#card-1 div p,
#card-2 p:first-child {
    font-size: 1.2rem;
    color: #B7A393;
}

#card-1 h1,
#card-2 h1{
    font-size: 2rem;
    color: #5b3926;
}

#card-2{
    padding: 2rem;
}

#cent{
    display: flex;
    align-items: center;
    justify-content: center;
}

#cent + div{
    margin-left: .5rem;
}

.card-dash{
    padding: .6rem 1rem;
    color: #B7A393;
    border-top: 1px solid rgb(226, 214, 207);
    display: flex;
    align-items: center;
    justify-content: center;
}

#dash1{
    justify-content: space-between;
}

#address{
    margin-top: 2rem;
    color: #b7a393;
    display: flex;
    align-items: center;
}

#address img{
    margin-right: .7rem;
    width: 2.5rem;
}

#address a{
    margin-left: .7rem;
    text-decoration: underline;
    color: #2280ff;
}

@media(max-width: 650px){
    #cards{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    
    #address{
        flex-direction: column;
    }
}
.all-farms{
    max-width: 900px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
    gap: 1rem;
}

.farms, .farm-box{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

#farm-head{
    margin: 1rem;
    text-align: center;
    color: #5b3926;
}

.farm-logo-box{
    padding: .5rem;
    box-shadow: 2px 2px 4px inset #707070, 
    -2px -2px 4px inset #707070;
    border-radius: 50%;
}

.farm-logo{
    width: 4rem;
}

.farm-box{
    position: relative;
    padding: 2rem;
    padding-top: .5rem;
    background-color: #F5F2F3;
    box-shadow: 3px 3px 6px #00000029;
    border: 1px solid rgb(226, 214, 207);
    border-radius: 20px;
}

.clock{
    position: absolute;
    right: -1px;
    top: 0;
}

.clock img{
    width: 6rem;
}

.clock p{
    position: absolute;
    top: 25px;
    right: 0;
    bottom: 22px;
    color: #fff;
    transform: rotate(45deg);
}

.farm-box h1{
    color: #5A5557;
}

.farm-box button{
    padding: .5rem;
    background-color: #F5F2F3;
    text-align: center;
    border-radius: 20px;
    box-shadow: rgb(226, 214, 207) 6px 6px 12px;
}

.farm-box button a{
    color: #83576F;
}

.farm-logo-box,
.farm-box h1, 
.farm-box p:first-child, 
.farm-box > a,
.farm-box button{
    margin-top: 1rem;
}

.lp{
    font-size: 1.2rem;
    color: #83576F;
}

.earn{
    font-size: 1.1rem;
    color: #83576F;
}

.farm-box > a{
    text-decoration: underline;
    color: #969294;
}

@media(max-width: 900px){
    .all-farms{
        grid-template-columns: repeat(2, 1fr);
    }    
}

@media(max-width: 750px){
    .all-farms{
        grid-template-columns: repeat(1, 1fr);
    }    
}
footer{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 4rem;
}

footer a{
    color: #847F80;
    padding: 1rem;
}

@media(max-width: 750px){
    footer{
        margin-top: 3rem;
    }
    
    footer a{
        padding: .8rem;
    }    
}

@media(max-width: 450px){
    footer{
        margin-top: 2rem;
    }
    
    footer a{
        padding: .5rem;
    }    
}
