@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');

#app{
    font-family: 'Nunito', sans-serif;
    min-width: 100vw;
    min-height: 100vh;
    width: 100%;
    height: 100%;
    background-color: #cfd9d0;
}

#app-container{
    max-width: 1200px;
    margin: 0 auto;
}

.logo{
    width: 3rem;
}

@media(max-width: 1200px){
    #app-container{
        padding: 1rem 2rem;
    }
}
