#logo{
    margin-bottom: 1.5rem;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#logo img{
    width: 12rem;
}

#logo h1{
    position: absolute;
    bottom: -20px;
    color: #5A5557;
}

@media(max-width: 750px){
    #logo img{
        width: 10rem;
    }    
}

@media(max-width: 450px){
    #logo img{
        width: 8rem;
    }    
}
