#home{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 800px;
    margin: 0 auto;
}

#home h3, 
#home button{
    margin-top: 1rem;
}

#home h3{
    color: #5A5557;
    text-align: center;
}

#home button{
    font-size: larger;
    padding: 1rem;
    width: 100%;
    background-color: rgb(240, 233, 231);
    border-radius: 20px;
    box-shadow: rgb(226, 214, 207) 6px 6px 12px;
}

#home button a{
    color: #dd6a0d;
}

#home button:hover{
    background-color: rgb(247, 244, 242);
}

#cards{
    width: 100%;
    margin-top: 2rem;
    color: #b7a393;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    justify-content: space-between;
}

.card{
    display: grid;
    grid-template-rows: 1fr;
    background-color: #F0E9E7;
    border: 1px solid rgb(226, 214, 207);
    border-radius: 10px;
    box-shadow: rgb(247, 244, 242) 1px 1px 0px inset;
}

#card-1{
    padding: 2rem;
    display: flex;
}

#card-1 div p,
#card-2 p:first-child {
    font-size: 1.2rem;
    color: #B7A393;
}

#card-1 h1,
#card-2 h1{
    font-size: 2rem;
    color: #5b3926;
}

#card-2{
    padding: 2rem;
}

#cent{
    display: flex;
    align-items: center;
    justify-content: center;
}

#cent + div{
    margin-left: .5rem;
}

.card-dash{
    padding: .6rem 1rem;
    color: #B7A393;
    border-top: 1px solid rgb(226, 214, 207);
    display: flex;
    align-items: center;
    justify-content: center;
}

#dash1{
    justify-content: space-between;
}

#address{
    margin-top: 2rem;
    color: #b7a393;
    display: flex;
    align-items: center;
}

#address img{
    margin-right: .7rem;
    width: 2.5rem;
}

#address a{
    margin-left: .7rem;
    text-decoration: underline;
    color: #2280ff;
}

@media(max-width: 650px){
    #cards{
        grid-template-columns: 1fr;
        gap: 1rem;
    }
    
    #address{
        flex-direction: column;
    }
}