nav, #nav-logo, #nav-links{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#nav-logo strong{
    color: #847F80;
    padding: 0 .3rem ;
}

#nav-links a{
    color: #847F80;
    padding: 0 1rem ;
}

#nav-links a:hover{
    color: #ff3535;
}

#nav-button button{
    cursor: pointer;
    padding: .2rem .5rem;
    background-color: #fff;
    color: #ff3535;
    border-radius: 16px;
    box-shadow: rgb(226, 214, 207) 4px 4px 8px;
}

#nav-button button:hover{
    transform: scale(1.1);
}

@media(max-width: 750px){
    #nav-links a{
        padding: 0 .7rem ;
    }    
}

@media(max-width: 450px){
    #nav-links a{
        padding: 0 .5rem ;
    }    

    #nav-logo p{
        display: none;
    }
}


@media(max-width: 400px){
    #nav-links a{
        padding: 0 .3rem ;
    }    

    nav{
        font-size: .8rem;
    }
}

