footer{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 4rem;
}

footer a{
    color: #847F80;
    padding: 1rem;
}

@media(max-width: 750px){
    footer{
        margin-top: 3rem;
    }
    
    footer a{
        padding: .8rem;
    }    
}

@media(max-width: 450px){
    footer{
        margin-top: 2rem;
    }
    
    footer a{
        padding: .5rem;
    }    
}