.all-farms{
    max-width: 900px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
}

.farms, .farm-box{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

#farm-head{
    margin: 1rem;
    text-align: center;
    color: #5b3926;
}

.farm-logo-box{
    padding: .5rem;
    box-shadow: 2px 2px 4px inset #707070, 
    -2px -2px 4px inset #707070;
    border-radius: 50%;
}

.farm-logo{
    width: 4rem;
}

.farm-box{
    position: relative;
    padding: 2rem;
    padding-top: .5rem;
    background-color: #F5F2F3;
    box-shadow: 3px 3px 6px #00000029;
    border: 1px solid rgb(226, 214, 207);
    border-radius: 20px;
}

.clock{
    position: absolute;
    right: -1px;
    top: 0;
}

.clock img{
    width: 6rem;
}

.clock p{
    position: absolute;
    top: 25px;
    right: 0;
    bottom: 22px;
    color: #fff;
    transform: rotate(45deg);
}

.farm-box h1{
    color: #5A5557;
}

.farm-box button{
    padding: .5rem;
    background-color: #F5F2F3;
    text-align: center;
    border-radius: 20px;
    box-shadow: rgb(226, 214, 207) 6px 6px 12px;
}

.farm-box button a{
    color: #83576F;
}

.farm-logo-box,
.farm-box h1, 
.farm-box p:first-child, 
.farm-box > a,
.farm-box button{
    margin-top: 1rem;
}

.lp{
    font-size: 1.2rem;
    color: #83576F;
}

.earn{
    font-size: 1.1rem;
    color: #83576F;
}

.farm-box > a{
    text-decoration: underline;
    color: #969294;
}

@media(max-width: 900px){
    .all-farms{
        grid-template-columns: repeat(2, 1fr);
    }    
}

@media(max-width: 750px){
    .all-farms{
        grid-template-columns: repeat(1, 1fr);
    }    
}